@import "../utils/colors.scss";

.navbar {
    margin: 2rem 0;
    display: flex;
    justify-content: space-between;

    .col {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .collapsable-button {
            display: none;
        }
    }

    nav {
        .links {
            ul {
                display: flex;
                list-style: none;
                align-items: center;
                justify-content: center;
                margin: 0.3rem 2rem;
                color: $lightFontColor;

                li {
                    text-transform: uppercase;
                    margin: 0 1rem;

                    a {
                        text-decoration: none;
                        font-size: 1.2rem;
                        color: $blueColor;

                        &:hover {
                            color: $pinkColor;
                            font-weight: 600;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 320px) and (max-width: 768px) {
    .navbar.active {
        nav {
            display: block;
        }
    }

    .navbar {
        flex-direction: column;
        justify-content: flex-start;

        .col {
            .collapsable-button {
                display: block;
                font-size: 4rem;
            }
        }

        nav {
            display: none;

            .links {
                ul {
                    flex-direction: column;
                    align-items: flex-start;
                    margin: 0.3rem 0rem;

                    li {
                        margin: 2rem 0 0 0;

                        &:first-of-type {
                            margin-top: 4rem;
                        }

                        a {
                            font-size: x-large;
                        }
                    }
                }
            }
        }
    }
}